<template>
  <transition name="shadow">
    <div v-if="openShadow" class="shadow-wrapper"></div>
  </transition>
  <div class="header">
    <div class="button" @click="open_inventory()">
      <Cat/>
    </div>
    <div class="button" @click="open_leaderboard()">
      <Leader/>
    </div>
  </div>
  <div class="wrapper">
    <Box @click="now_point += 1"/>
    <TitleLevel :level="level" :nowPoints="now_point" :nextLevelPoints="next_level_points"/>
    <ProgresBar :nextLevelPoints="next_level_points" :nowPoints="now_point"/>
  </div>
  <transition name="inventory">
    <InvetoryView v-if="openInventory" @close="close_inventory()"/>
  </transition>
  <transition name="leaderboard">
    <LeaderboardView v-if="openLeaderboard" @close="close_leaderboard()"/>
  </transition>
  

</template>

<script setup>
import Box from '@/components/Box.vue'
import ProgresBar from '@/components/ProgresBar.vue'
import TitleLevel from '@/components/TitleLevel.vue'
import Leader from './components/icons/Leader.vue'
import Cat from '@/components/icons/Cat.vue'
import {ref, watch} from 'vue'
import InvetoryView from './components/views/InvetoryView.vue'
import LeaderboardView from './components/views/LeaderboardView.vue'


const next_level_points = ref(20)
const now_point = ref(0)
const level = ref(0)
const openInventory = ref(false)
const openLeaderboard = ref(false)
const openShadow = ref(false)

watch(now_point, async () => {
  if(now_point.value >= next_level_points.value) {
    console.log('next_level')
    next_level()
  }
})

function next_level() {
  level.value += 1
  now_point.value = 0
  next_level_points.value += 20
}

function open_inventory() {
  openInventory.value = true
  openShadow.value = true
}

function close_inventory() {
  openInventory.value = false
  openShadow.value = false
}

function open_leaderboard() {
  openLeaderboard.value = true
  openShadow.value = true
}

function close_leaderboard() {
  openLeaderboard.value = false
  openShadow.value = false
}
</script>

<style>
