  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

body {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.title {
  margin-top: 5vw;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 20vh;
  max-height: 80px;
}

.button {
  width: 64px;
  height: 64px;
  border-radius: 32px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button:active {
  background-color: #f0f0f0;
  transform: scale(0.9);
  transition: transform .5s;
}

.shadow-wrapper {
  opacity: 0.5;
  background-color: #2c3e50;
  position: absolute;
  height: 100%;
  width: 100%;
}

.inventory-enter-active {
  animation: toogle-view 0.5s;
}
.inventory-leave-active {
  animation: toogle-view 0.5s reverse;
}

.leaderboard-enter-active {
  animation: toogle-view 0.5s;
}
.leaderboard-leave-active {
  animation: toogle-view 0.5s reverse;
}

.shadow-enter-active {
  animation: shadow-view 0.5s;
}

.shadow-leave-active {
  animation: shadow-view 0.5s reverse;
}

@keyframes toogle-view {
  0% {
    top: 100%;
  }
  100% {
    top: 0;
  }
}

@keyframes shadow-view {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
</style>
