<template>

<div class="loader-bar">
  <div class="block-border"></div>
 <div class="block-border"></div>
  <div class="block-border"></div>
  <div class="block-border"></div>
  <div class="block-border"></div>
  <div class="block-border"></div>
  <div class="block-border"></div>
  <div class="block-border"></div>
  <div 
    class="block-meter" 
    v-for="item in progressActual"
    :key="item"
    :class="{progress: item == 'progress', empty: item == 'empty'}"></div>
</div>
</template>

<script setup>
import {computed, ref, onMounted} from 'vue'

const props = defineProps(['nowPoints', 'nextLevelPoints'])

const pointPerMeter = computed(() => {return Math.round(props.nextLevelPoints / 8)})
const progressMeter = ref([
  "empty",
  "empty",
  "empty",
  "empty",
  "empty",
  "empty",
  "empty",
  "empty",
  "empty",
  "empty"
])
const progressActual = computed(() => {
  let progress = []
  progressMeter.value.forEach((item, index) => {
    if(index*pointPerMeter.value < props.nowPoints) {
      progress.push("ready")
    } else if (index*pointPerMeter.value - pointPerMeter.value < props.nowPoints) {
      progress.push("progress")
    } else {
      progress.push("empty")
    }
  })
  return progress
})


onMounted(() => {

})
</script>

<style scoped>
.loader-bar {
  width: 35vw;
  height: 6vw;
  display: grid;
  grid-template-columns: repeat(3,1vw) repeat(10, 2vw 1vw) repeat(2,1vw);
  grid-template-rows: repeat(6,1vw);
}

.block-border {
  background-color: rgb(50,50,50);
}

.block-meter {
  background-color: rgb(100,200,100);
}

.block-border:nth-of-type(1) {
  grid-row: 1;
  grid-column: 3/-3;
}

.block-border:nth-of-type(2) {
  grid-row: -2;
  grid-column: 3/-3;
}

.block-border:nth-of-type(3) {
  grid-row: 3/5;
  grid-column: 1;
}

.block-border:nth-of-type(4) {
  grid-row: 3/5;
  grid-column: -2;
}

.block-border:nth-of-type(5) {
  grid-row: 2;
  grid-column: 2;
}

.block-border:nth-of-type(6) {
  grid-row: 2;
  grid-column: -3;
}

.block-border:nth-of-type(7) {
  grid-row: 5;
  grid-column: 2;
}

.block-border:nth-of-type(8) {
  grid-row: 5;
  grid-column: -3;
}

.block-meter:nth-of-type(9) {
  grid-row: 3/5;
  grid-column: 4;
}

.block-meter:nth-of-type(10) {
  grid-row: 3/5;
  grid-column: 6;
}

.block-meter:nth-of-type(11) {
  grid-row: 3/5;
  grid-column: 8;
}

.block-meter:nth-of-type(12) {
  grid-row: 3/5;
  grid-column: 10;
}

.block-meter:nth-of-type(13) {
  grid-row: 3/5;
  grid-column: 12;
}

.block-meter:nth-of-type(14) {
  grid-row: 3/5;
  grid-column: 14;
}

.block-meter:nth-of-type(15) {
  grid-row: 3/5;
  grid-column: 16;
}

.block-meter:nth-of-type(16) {
  grid-row: 3/5;
  grid-column: 18;
}

.block-meter:nth-of-type(17) {
  grid-row: 3/5;
  grid-column: 20;
}

.block-meter:nth-of-type(18) {
  grid-row: 3/5;
  grid-column: 22;
}

.empty {
  opacity: 0;
}

.progress {
  opacity: 0;
  animation: blinky2 2s 1s linear infinite;
}

@keyframes blinky1 {
  99% {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes blinky2 {
  49% {opacity: 0;}
  50% {opacity: 1;}
  90% {opacity: 1;}
}
</style>